@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.sliderWrapper{
    background-color: #f8f9fa;
    padding:120px 15px 80px;
}
.sliderWrapper .Content-slider{
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
}
.sliderWrapper .Content-slider h1{
    font-size: 60px;
    text-transform: uppercase;
    font-weight: 600;
}
.sliderWrapper .Content-slider p{
    font-size: 18px;
    line-height: 30px;
    font-weight: 400;
    letter-spacing: 1px;
}
.sliderWrapper .slick-dots{
    background: #41dfc0;
    max-width: 150px;
    display: flex !important;
    justify-content: center;
            border-radius: 17px;
            bottom: 5px;
            padding: 10px 15px;
            bottom: -30px;
            padding: 10px 15px;
            left: 50%;
            transform: translateX(-50%);
}
.sliderWrapper .slick-dots li button:before{
    line-height: normal;
    line-height: initial;
    color: transparent;
}
.slick-slide *{
    outline: 0 !important;
}
.slick-dots li button:before,
.slick-dots li button,
.slick-dots li{
    height: auto;
    width: auto;
}
.slick-dots li button{
    background: #fff;
    border-radius: 9px;
    width: 15px;
    height: 15px;
}
.slick-dots li.slick-active button {
    opacity: 0.7;
}

body{font-family:"Roboto",sans-serif;color:#21363d}.section{padding:60px 0}.section-title{text-align:center;font-weight:600;font-size:40px;margin-bottom:40px;display:inline-block;position:relative;transition:0.2s ease-in-out}.section-title::before{content:'\f0da';font-family:FontAwesome;color:#41dfc0;left:-15px;position:relative;transition:0.2s ease-in-out}.section-title:hover{color:#41dfc0}.section-title:hover::before{left:0px}.main-header{padding:20px 15px;box-shadow:0 0 20px #ccc;position:fixed;width:100%;z-index:99}.main-header .navbar-collapse .nav-link{font-size:16px;font-size:16px;padding:0 16px;text-transform:uppercase;font-weight:400}@media (max-width: 1024px){.main-header .navbar-nav{margin-left:auto !important;margin:unset !important}}@media (max-width: 991px){.main-header .container{position:relative}.main-header .container .navbar-collapse.collapse:not(.show){display:block}.main-header .container .navbar-collapse .navbar-nav{position:fixed;left:-50%;transition:0.2s ease-in-out;background:#21363d;height:100%;top:0;padding:30px 27px 15px}.main-header .container .navbar-collapse .navbar-nav .nav-link{color:#fff;padding-bottom:20px}.main-header .container .navbar-collapse.show .navbar-nav{left:0}}.main-btn{border-radius:0;line-height:normal;line-height:initial;padding:12px 30px;text-transform:uppercase;transition:0.2s ease-in-out;border-color:#21363d;background-color:#21363d;font-size:18px;font-weight:600}.main-btn:hover{border-color:#238673;background-color:#238673}.main-btn:focus{box-shadow:none !important}.banner-btn{border-width:2px;background:transparent;color:#21363d;white-space:nowrap}@media (max-width: 1025px){.banner-btn{position:absolute;right:15px}}@media (max-width: 991px){.banner-btn{position:absolute;right:85px;top:-3px}}.brands ul{padding:0;margin:0}.brands ul li{list-style:none;padding:0 15px}

